<template>
  <el-header :style="{'background-color': this.theme}">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      text-color="#fff"
      :background-color="this.theme"
      active-text-color="#ffd04b">
      <el-menu-item index="1"><router-link to="/">首页</router-link></el-menu-item>
      <el-menu-item index="2"><a href="https://www.jianshu.com/u/73ed03495ab6" target="_blank">简书</a></el-menu-item>
      <el-menu-item index="3"><a href="https://github.com/nnqiu" target="_blank">github</a></el-menu-item>
      <el-menu-item index="4"><router-link to="/home">home</router-link></el-menu-item>                                                                                        
    </el-menu>
    <div class="header-right">
      <router-link to="/me">
        <img class="header-img" src="../../assets/header.jpg" alt="">
      </router-link>
      <el-color-picker
        v-model="theme"
        class="theme-picker"
        popper-class="theme-picker-dropdown"
      />
      <div style="width: 100%;height: 200px;">

      </div>
    </div>
  </el-header>
</template>

<script>

const version = require('element-ui/package.json').version // element-ui version from node_modules
const ORIGINAL_THEME = '#303d4a' // default color

  export default {
    data() {
      return {
        activeIndex: '1',
        theme: ORIGINAL_THEME
      }   
    },
    watch: {
      theme(val) {
        if (typeof val !== 'string') return
        // 传值给store
        this.$store.state.themeColor = val
        this.$store.commit('updatethemeColor', val)
      }
    },
    methods: {
      handleSelect() {

      },
    }
  }
</script>

<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  } 
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-menu-demo {
    float: left;
  }

  .el-color-picker {
    margin-left: 16px;
    margin-top: 10px;
  }

  .header-right {
    float: right;
  }

  .header-img {
    height: 30px;
    width: 30px;
    border-radius: 50%;
  }
</style>

